import { useContext, useEffect } from 'react'
import { ModalContext } from '../../../Context/ModalContext'
import { PhaseContext } from '../../../Context/PhaseContext'
import { UserDataContext } from '../../../Context/UserDataContext'
import s from './Name.module.scss'

export default (props) => {
    const data = useContext(UserDataContext)
    const phase = useContext(PhaseContext)
    const modal = useContext(ModalContext)

    const formHandler = (e) => {
        phase.setPhase('Iknow')
        modal.setModalVisibility(true)
        modal.setModalContent({mood: 'nya', content: `Тебя реально зовут ${data.userData.name}? ну и хуйня`})
    }


    useEffect(() => {
        modal.setModalVisibility(true)
        modal.setModalContent({content: 'Давай я сделаю вид что мне интересно как тебя зовут, впиши имя в поле'})
    }, [])

    return(
        <div className={s.name}>
            <form onSubmit={e => {
                e.preventDefault()
                formHandler(e)
            }} >
                <input autoFocus={true} onBlur={data.userData.name && formHandler} value={data.userData.name} onChange={e => data.setUserData({...data.userData, name: e.target.value.toUpperCase()})} type="text" />
            </form>
        </div>
    )
}